.contact-wrap {
  width: 750px;
  margin: auto;
  display: block; }

.contact-row {
  display: flex;
  justify-content: space-between; }

.contact-textarea {
  width: 100%;
  height: 250px;
  box-sizing: border-box; }

.contact-button {
  width: 200px;
  padding: .5em;
  background-color: white;
  font-size: 1.15em;
  cursor: pointer;
  border: 2px solid #45B8D8;
  color: #45B8D8;
  margin: auto;
  margin-top: 1em;
  margin-bottom: 2em;
  display: block;
  outline: none; }
  .contact-button:hover {
    background-color: #45B8D8;
    color: white; }
